'use strict';

var $ = require('nd-jquery');
var __ = require('nd-i18n');

function sizePrettify(size) {
  var ENUM = [' B', ' KB', ' MB', ' GB', ' TB'];

  var i = 0,
    kilo = 1024;

  while (size > kilo) {
    size /= kilo;
    i++;
  }

  return size.toFixed(2).replace(/\.00|0$/g, '') + ENUM[i];
}

module.exports = function() {
  var plugin = this,
    host = plugin.host;

  var hasErr;
  var restoreValue;
  var maxcount = host.get('maxcount');

  var _messages = {
    'Q_EMPTY': __(''),
    // 
    // 'Q_EXCEED_SIZE_LIMIT':  __(' ') + host.get('maxbytesq'),
    // 
    'Q_EXCEED_NUM_LIMIT': __(' ') + maxcount + __(' '),
    'Q_TYPE_DENIED': __(' ') + host.get('accept').mimeTypes + __(' '),
    'Q_EMPTY_FILE': __(' {name} '),
    // 
    'F_EXCEED_SIZE': __(' ') + sizePrettify(host.get('maxbytes')),
    'F_DUPLICATE': __('')
  };

  var container = $('<div class="ui-upload-message" />')
    .on('mouseout', function(e) {
      setTimeout(function() {
        e.target.style.display = 'none';
        if (restoreValue) {
          restoreValue = false;
          host.set('value', '');
        }
      }, 500);
    })
    .appendTo(host.element);

  var showMessage = function(text) {
    if (!host.get('value')) {
      restoreValue = true;
      host.set('value', 'fake');
    }

    container.text(text).show();
  };

  var hideMessage = function(text) {
    container.text(text).hide();
    host._blurTrigger();
  };

  host.on('error', function(type, arg1) {
    var template = _messages[type];

    if (template) {
      if (/\{.+\}/.test(template)) {
        template = template.replace(/\{(.+?)\}/, function(_, $1) {
          return arg1 && arg1[$1] || '';
        });
      }
    }

    hasErr = true;
    showMessage(template || __(''));
  });

  host.on('upload beforeFileQueued', function() {
    if (hasErr) {
      hasErr = false;
      hideMessage('');
    }
  });

  // 
  if (maxcount) {
    // webuuploader  maxcount 
    // maxcount 
    host.on('beforeFileQueued', function() {
      if (host.get('files').length >= maxcount) {
        host.trigger('error', 'Q_EXCEED_NUM_LIMIT');
        return false;
      }
    });
  }

  host.before('destroy', function() {
    container.off().remove();
  });

  // 
  this.ready();
};



